body {
    margin: 0;
    font-family: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

code {
    font-family: source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace
}

header {
    padding: 30px 0;
    position: relative;
    z-index: 20
}

header#header-wrap {
    display: flex;
    justify-content: center
}

header#header-wrap #logo {
    width: 35%
}

header#header-wrap #m-nav-div {
    display: none
}

header#header-wrap #m-nav-div span {
    display: block;
    width: 35px;
    height: 3px;
    background: #333;
    margin: 4px 0
}

header#header-wrap nav {
    width: 60%;
    cursor: pointer
}

header#header-wrap nav #lang {
    display: flex;
    justify-content: flex-end
}

header#header-wrap nav #lang p {
    text-align: right;
    height: 100%;
    position: relative
}

header#header-wrap nav #lang p.lang-tit {
    color: #888
}

header#header-wrap nav #lang p:hover li {
    display: block
}

header#header-wrap nav #lang ul {
    text-align: center;
    transition: .5s;
    position: absolute;
    left: -10px;
    top: 22px;
    z-index: 20;
    color: #333
}

header#header-wrap nav #lang ul li {
    padding: 0 10px;
    background: #ebebeb;
    border-bottom: 1px solid #ccc;
    display: none
}

header#header-wrap nav #main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    margin-top: 5px;
    width: 100%
}

header#header-wrap nav #main-nav>li {
    position: relative;
    width: 16.66%;
    height: 30px
}

header#header-wrap nav #main-nav>li.nav-tit {
    font-size: 20px;
    font-weight: 600
}

header#header-wrap nav #main-nav>li:hover #sub-nav {
    display: block
}

header#header-wrap nav #main-nav>li #sub-nav {
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    position: absolute;
    left: 0;
    top: 32px;
    background: #fff;
    box-shadow: 2px 2px 8px #666;
    display: none;
    transition: .5s
}

header#header-wrap nav #main-nav>li #sub-nav li {
    padding: 10px
}

@media (max-width: 1023px) and (min-width:768px) {
    header#header-wrap {
        justify-content:space-between
    }

    header#header-wrap #logo {
        padding-left: 30px
    }

    header#header-wrap #m-nav-div {
        display: block;
        padding-right: 30px
    }

    header#header-wrap #m-nav-div.on #m-nav {
        right: 0
    }

    header#header-wrap #m-nav-div #m-nav-icon {
        cursor: pointer
    }

    header#header-wrap #m-nav-div #m-nav-icon.on #m-nav {
        right: 0
    }

    header#header-wrap #m-nav-div #m-nav {
        width: 30%;
        height: 100vh;
        position: fixed;
        z-index: 150;
        right: -100%;
        top: 65px;
        background: #ebebeb;
        transition: 1s
    }

